<template>
  <v-container fluid fill-height style="padding:0;" id="eventsource" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="EventSource">EventSource</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>EventSource</code>对象可以用来建立长连接，保证后端持续传递数据给前端，示例代码如下</p>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    js前端代码
        const es = new EventSource('/api/test');
        es.onopen = function(e) {
        };
        es.onmessage = function(e) {
            // 服务端每次传递数据都会调用此方法
            console.log(e.data);
        };
        es.onerror = function(err) {
            es.close();
        };

    node服务度代码
        app.get("/api/test", function(req, res, next){
            res.writeHead(200, {
                "Content-Type":"text/event-stream", 
                "Cache-Control":"no-cache", 
                "Connection":"keep-alive"
            });

            res.write("retry: 10000\\n");
            res.write("event: connecttime\\n");

            setInterval(function(){
                res.write("data: " + new Date() + "\\n\\n");
            }, 1000)
        })
    `
],
      time: "2019-07-28",
      title: "EventSource",
      items: [
        {
          title: "EventSource",
          target: "#EventSource"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#eventsource {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

